import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Jumbotron,
    Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isNavOpen: false
        };
        this.toggleNav = this.toggleNav.bind(this);
    }

    toggleNav() {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }

    render(){
        return(
            <React.Fragment>
                <Navbar dark color="dark" expand="md">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav} />
                        <NavbarBrand className="mr-auto" href="/">
                           <strong>DROP</strong> 
                        </NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link mx-2" to="/home">
                                        <span className="fa fa-home fa-lg"></span> Home
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link mx-1" to="/aboutus">
                                        <span className="fa fa-info fa-lg"></span> About Us
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link mx-2" to="/service">
                                        <span className="fa fa-briefcase fa-lg"></span> Services
                                    </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link mx-1" to="/liscence">
                                        <span className="fa fa-book fa-lg"></span> Liscence
                                    </NavLink>
                                </NavItem> 
                                &nsbp;&nsbp;&nsbp;&nsbp;&nsbp;&nsbp;&nsbp;&nsbp;&nsbp;
                                <div class="navbar-nav ml-6">
                                    <a data-toggle="tooltip" title="Facebook" href="#" class="nav-item nav-link"><i class="fa fa-lg fa-facebook"></i></a>
                                    <a data-toggle="tooltip" title="Twitter" href="#" class="nav-item nav-link"><i class="fa fa-lg fa-twitter"></i></a>
                                    <a data-toggle="tooltip" title="Instagram" href="#" class="nav-item nav-link"><i class="fa fa-lg fa-instagram"></i></a>
                                    <a data-toggle="tooltip" title="LinkedIn" href="#" class="nav-item nav-link"><i class="fa fa-lg fa-linkedin"></i></a>
                                </div>
                            </Nav>
                            
                        </Collapse>
                    </div>
                </Navbar>
            </React.Fragment>
        );
    }
}

export default Header;